/* SearchPage.css */

/* The main container for the search page */

.search-container {
    position: relative; /* so side-buttons can be positioned if needed */
    min-height: calc(100vh - 60px); /* subtract header height if your Layout header is 60px tall */
    background-color: #E5E5E5; /* matches the screenshot's gray */
    padding: 40px 20px;
  }
  
  /* Centered title */
  .search-title {
    text-align: center;
    font-size: 24px;
    font-weight: bold;
    color: #6B6B6B;
    margin-bottom: 40px;
  }
  
  /* Center the search bar horizontally */
  .search-bar {
    display: flex;
    justify-content: center;
    margin-bottom: 20px;
  }
  
  /* Wide input field */
  .search-input {
    width: 50%;
    height: 40px;
    padding: 10px;
    font-size: 16px;
    border: 1px solid #ccc;
    border-radius: 4px;
  }

  .search-button {
    margin-left: 10px;
    background-color: #00004D;
    color: white;
    padding: 10px 15px;
    border: none;
    border-radius: 4px;
    font-size: 14px;
    cursor: pointer;
    font-weight: bold;
  }
  .search-button:hover {
    background-color: #000066;
  }
  
  /* Position the side buttons on the right side, stacked vertically */
  .side-buttons {
    position: absolute;
    top: 50%;
    right: 40px;
    transform: translateY(-50%);
    display: flex;
    flex-direction: column;
    gap: 10px;
  }
  
  /* Blue buttons to match the rest of the UI */
  .side-button {
    background-color: #00004D;
    color: white;
    padding: 10px 15px;
    border: none;
    border-radius: 4px;
    font-size: 14px;
    cursor: pointer;
    font-weight: bold;
  }
  
  .side-button:hover {
    background-color: #000066;
  }
  