/* Layout.css */
@font-face {
    font-family: 'Vilane';
    src: url('./assets/fonts/Vilane Extra Light.ttf') format('ttf');
         

  }
  
.layout {
    min-height: 100vh;
    background-color: #E5E5E5; /* Your chosen background color */
  }
  
  .header {
    background-color: #00004D; /* Blue header */
    height: 60px;
    display: flex;
    align-items: center;
    padding-left: 20px;
  }
  .header-logo {
    height: 40px;         /* Adjust as needed */
    margin-right: 10px;     /* Space between logo and text */
  }
  
  .header-text {
    color: white;
    
    font-size: 24px;
    font-weight: bold;
  }
  
  .content {
    position: relative;
    /* This container will hold the transitioning content */
  }
  