/* ProfileView.css */

body {
    background-color: #f2f2f2;
    margin: 0;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  }
  .profile-wrapper {
    display: flex;
    justify-content: center;
    padding: 3rem 1rem;
  }

  .profile-container {
    background-color: #fff; /* White card */
    border-radius: 10px;
    box-shadow: 0 1px 8px rgba(0, 0, 0, 0.08);
    padding: 3rem 2rem;
    max-width: 900px;
    width: 100%;
  }
  
  
  h2 {
    text-align: center;
    margin-bottom: 0.25rem;
  }
  
  .profile-summary {
    text-align: center;
    font-size: 0.95rem;
    color: #666;
    margin-bottom: 2rem;
  }
  
  /* Back Button */
  .back-button {
    background-color: #0a0a56;
    color: white;
    border: none;
    padding: 0.5rem 1rem;
    margin-bottom: 2rem;
    border-radius: 6px;
    cursor: pointer;
    font-size: 0.9rem;
  }
  
  .back-button:hover {
    background-color: #1a1a7a;
  }
  
  /* Accordion Styling */
  .accordion-section {
    margin-bottom: 1rem;
    border-radius: 6px;
    box-shadow: 0 1px 4px rgba(0, 0, 0, 0.08);
    background-color: #fff;
    overflow: hidden;
  }
  
  .accordion-header {
    background-color: #fff;
    border: none;
    width: 100%;
    text-align: left;
    padding: 1rem 1.25rem;
    font-size: 1rem;
    font-weight: 600;
    cursor: pointer;
    outline: none;
    display: flex;
    justify-content: space-between;
  }
  
  .accordion-header:hover {
    background-color: #f5f5f5;
  }
  
  .accordion-content {
    padding: 1rem 1.25rem;
    background-color: #fafafa;
    font-size: 0.95rem;
    color: #333;
    animation: fadeDown 0.2s ease-in-out;
  }
  
  @keyframes fadeDown {
    from {
      opacity: 0;
      transform: translateY(-3px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }
  
  /* Export Button Styling */
  .export-container {
    text-align: right;
    margin-top: 2rem;
  }
  
  .export-button {
    background-color: #0a0a56;
    color: white;
    border: none;
    padding: 0.6rem 1.3rem;
    font-size: 0.9rem;
    font-weight: bold;
    border-radius: 6px;
    cursor: pointer;
  }
  
  .export-button:hover {
    background-color: #1a1a7a;
  }
  